html {
  display: block;
  position: relative;
  overflow-x: hidden;
}

body {
  background: #f1f1f1;
  font-family: "Nunito Sans";
  z-index: 1;
  position: relative;
  overflow-x: hidden;
  font-size: 8px;
}

section {
  padding: 80px 0 0;
  position: relative;
  z-index: 2;
}

.title {
  font-weight: 800;
  font-size: 4.5em;
  line-height: 1;
  color: #1b1a1a;
  margin: 0 0 30px;
}
.title span {
  color: #6c63ff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-anim {
  opacity: 0;
  transform: translateY(40px);
  transition: all 1s;
}
.section-anim.section-anim__active {
  opacity: 1;
  transform: translateY(0);
}

.patern-bg {
  position: relative;
  z-index: 2;
  overflow: hidden;
}
.patern-bg::before {
  content: "";
  display: block;
  position: absolute;
  inset: 0;
  background: url("../img/patern.jpg") center repeat;
  background-size: cover;
  opacity: 0.6;
  filter: blur(10px);
  z-index: -1;
  pointer-events: none;
}

.input {
  position: relative;
}
.input p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.3s;
  transform-origin: 0% 50%;
  pointer-events: none;
}
.input input {
  background: transparent;
  padding: 0 0 5px;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  box-shadow: none;
  color: #f1f1f1;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
}
.input input:focus, .input input:active, .input input:not(:placeholder-shown) {
  border-bottom: 1px solid #fff;
}
.input input:focus + p, .input input:active + p, .input input:not(:placeholder-shown) + p {
  transform: translateY(-100%) scale(0.8);
}
.input__check {
  position: relative;
  display: grid;
  grid-template-columns: 18px 1fr;
  gap: 10px;
}
.input__check p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
}
.input__check p a {
  color: #6c63ff;
  position: relative;
  display: inline-block;
  mix-blend-mode: lighten;
  white-space: nowrap;
}
.input__check p a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #6c63ff;
  transition: all 0.7s;
}
.input__check p a::after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  height: 1px;
  background: #6c63ff;
  transition: all 0.7s;
  opacity: 1;
  left: 0;
  width: 100%;
  transition-delay: 0.1s;
}
.input__check p a:hover::after {
  right: 0;
  width: 0;
  left: auto;
  transition-delay: 0s;
}
.input__check p a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
  transition-delay: 0.1s;
}
.input__check span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  border: 1px solid #fff;
  transition: all 0.3s;
}
.input__check span svg {
  opacity: 0;
  transition: all 0.3s;
}
.input__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
  font-family: "Nunito Sans";
}
.input__check input:checked + span {
  background: #fff;
}
.input__check input:checked + span svg {
  opacity: 1;
}

.btn__blc {
  border-radius: 10px;
  background: #6c63ff;
  padding: 11px 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #f2f99c;
  display: block;
  border: none;
  position: relative;
  cursor: pointer;
}
.btn__wht {
  border-radius: 10px;
  background: #fff;
  padding: 11px 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #1b1a1a;
  display: block;
  border: none;
  position: relative;
  cursor: pointer;
}
.btn__anim {
  display: block;
  position: relative;
}
.btn__anim b {
  display: block;
  position: relative;
  overflow: hidden;
}
.btn__anim i {
  display: block;
  width: 100%;
  position: relative;
}
.btn__anim i + i {
  position: absolute;
  left: 0;
  top: 100%;
}
.btn__anim span {
  display: inline-block;
  position: relative;
  transition: transform 0.6s cubic-bezier(0.76, 0, 0.24, 1);
}
.btn__anim:hover span {
  transform: translateY(-100%);
}

.nav {
  padding: 20px 0;
}
.nav__logo {
  max-width: 220px;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-content: start;
  align-items: center;
}
.nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
  margin: 0 32px 0 auto;
}
.nav ul a {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #030303;
  transition: all 0.3s;
  position: relative;
}
.nav ul a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #030303;
  transition: all 0.3s;
}
.nav ul a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
}
.nav__hum, .nav__soc {
  display: none;
}

.header {
  padding: 120px 0;
  background: #ece7e4;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.header .btn__blc {
  margin: 20px 0 0;
}
.header h1 {
  font-weight: 800;
  font-size: 8em;
  line-height: 1;
  color: #1b1a1a;
}
.header h1 span,
.header h1 strong,
.header h1 b {
  color: #6c63ff;
}
.header__subtitle {
  font-weight: 400;
  font-size: 3em;
  line-height: 1.1;
  color: #403e3b;
  margin: 30px 0 0;
  max-width: 800px;
}
.header .container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}
.header__form {
  padding: 30px 20px;
  border-radius: 30px;
  background: #403e3b;
  max-width: 400px;
}
.header__form_text {
  font-weight: 400;
  font-size: 2.5em;
  line-height: 1.1;
  color: #f1f1f1;
}
.header__form .btn__wht {
  width: 100%;
}
.header__form_row {
  margin: 30px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}
.header .input {
  margin: 30px 0 0;
}
.header .input__check {
  margin: 15px 0 0;
}

.why__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
}
.why__item {
  border-radius: 30px;
  padding: 20px;
  transition: all 0.3s;
  cursor: default;
  background: #EAE9FF;
}
.why__item svg {
  width: 36px;
  height: 36px;
}
.why__item svg path {
  fill: #6c63ff;
  transition: all 0.3s;
}
.why__item img {
  width: 36px;
  height: 36px;
  display: block;
}
.why__item p {
  font-weight: 600;
  font-size: 3em;
  line-height: 1.2;
  color: #030303;
  transition: all 0.3s;
  margin: 5px 0 0;
}
.why__item p + p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  margin: 12px 0 0;
  color: #403e3b;
}
.why__item .btn__blc {
  margin: 12px 0 0;
}

.service__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  z-index: 3;
  position: relative;
}

.work {
  position: relative;
  overflow: hidden;
  padding: 60px 0;
  margin: 60px 0 0;
}
.work__slider {
  position: relative;
  overflow: visible;
}
.work__item {
  position: relative;
  overflow: hidden;
  border-radius: 30px;
  border: 2px solid #EAE9FF;
  display: grid;
  grid-template-rows: auto 1fr;
  background: #f1f1f1;
}
.work__prev {
  position: relative;
  border-radius: 30px;
  overflow: hidden;
}
.work__prev img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  object-position: top left;
}
.work__info {
  padding: 15px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.work__descr {
  font-weight: 400;
  font-size: 2.5em;
  line-height: 1.2;
  color: #403e3b;
  transition: all 0.3s;
  margin: 0 0 auto;
}
.work__tag {
  margin: 10px 0 0;
}
.work__tag ul {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.work__tag ul li {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #403e3b;
  transition: all 0.3s;
  padding: 2px 10px;
  background: #EAE9FF;
  border-radius: 15px;
}
.work__link {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 10px;
  margin: 20px 0 0;
}
.work__link svg {
  width: 36px;
  height: 36px;
}

.faq {
  z-index: 1;
}
.faq__mob {
  display: none;
}
.faq__block {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 30px;
  position: relative;
  align-items: start;
}
.faq__title {
  width: 400px;
}
.faq__title.fix {
  position: fixed;
  top: 20px;
}
.faq__title.bottom {
  position: absolute;
  left: 0;
  bottom: 0;
}
.faq__title_text {
  font-size: 16px;
  color: #403e3b;
  margin: 0 0 20px;
  font-weight: 500px;
}
.faq__title_text span {
  color: #6c63ff;
}
.faq__wrapper {
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 1fr;
  gap: 0;
}
.faq__wrapper .btn__blc,
.faq__wrapper .faq__title_text {
  display: none;
}
.faq__item {
  border-bottom: 1px solid #030303;
  padding: 0 0 0;
}
.faq__item:last-child {
  padding: 0;
  border-bottom: none;
}
.faq__item:nth-child(1) .faq__btn {
  padding-top: 0;
}
.faq__info {
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s;
}
.faq__info p {
  font-size: 2.5em;
  color: #403e3b;
}
.faq__btn {
  display: grid;
  grid-template-columns: 1fr 20px;
  gap: 20px;
  align-items: center;
  cursor: pointer;
  padding: 20px 0;
}
.faq__btn p {
  font-size: 3em;
  font-weight: 600;
  transition: all 0.3s;
  color: #030303;
}
.faq__btn span {
  position: relative;
  display: block;
  width: 20px;
  height: 20px;
}
.faq__btn span::before, .faq__btn span::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #030303;
  transition: all 0.3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.faq__btn span::after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.faq__btn:hover p, .faq__btn.active p {
  color: #6c63ff;
}
.faq__btn:hover span::after, .faq__btn:hover span::before, .faq__btn.active span::after, .faq__btn.active span::before {
  background: #6c63ff;
}
.faq__btn.active span::after {
  transform: translate(-50%, -50%);
}
.faq__btn.active + .faq__info {
  max-height: 200px;
  padding: 0 0 20px;
}

.form .container {
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 30px;
}
.form__soc {
  margin: 20px 0 0;
}
.form__soc div {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 10px;
  margin: 20px 0 0;
}
.form__soc a {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  width: 45px;
  height: 45px;
  background: #403e3b;
}
.form__soc a svg {
  width: 30px;
  height: 30px;
}
.form__soc a svg path {
  fill: #f1f1f1;
  transition: all 0.3s;
}
.form__soc a:hover path {
  fill: #6c63ff;
}
.form__descr {
  display: flex;
  flex-direction: column;
  max-width: 600px;
}
.form__descr p {
  font-size: 2.5em;
  color: #403e3b;
  line-height: 1.2;
}

.footer {
  margin: 80px 0 0;
  padding: 50px 0 25px;
  background: #403e3b;
}
.footer__block {
  display: grid;
  grid-template-columns: 220px 1fr 3fr auto;
  gap: 35px;
  align-items: start;
}
.footer__descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #f1f1f1;
  transition: all 0.3s;
  position: relative;
}
.footer__logo img {
  width: 100%;
  filter: invert(1);
}
.footer__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  margin: 0 auto;
}
.footer__list a {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #f1f1f1;
  transition: all 0.3s;
  position: relative;
}
.footer__list a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #f1f1f1;
  transition: all 0.3s;
}
.footer__list a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
}
.footer__soc {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__soc a {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  background: #f1f1f1;
}
.footer__soc a svg {
  width: 24px;
  height: 24px;
}
.footer__soc a svg path {
  fill: #403e3b;
  transition: all 0.3s;
}
.footer__soc a:hover path {
  fill: #6c63ff;
}
.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin: 50px 0 0;
}
.footer__row p,
.footer__row a {
  color: #f1f1f1;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
}
.footer__row a {
  transition: all 0.3s;
}
.footer__row a:hover {
  color: #6c63ff;
}

.price {
  padding: 60px 0;
  margin: 60px 0 0;
}
.price__block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.price__title {
  max-width: 500px;
}
.price__title p {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  margin: 12px 0 0;
  color: #403e3b;
}
.price__form {
  padding: 30px 20px 50px;
  border-radius: 30px;
  background: #403e3b;
  display: flex;
  gap: 0;
  position: relative;
  overflow: hidden;
}
.price__step {
  width: calc(100% + 20px);
  padding: 0 20px 0 0;
  min-width: calc(100% + 20px);
  transition: all 0.3s;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.price__step_title {
  font-weight: 400;
  font-size: 3em;
  line-height: 1.1;
  color: #f1f1f1;
}
.price__step_title span {
  color: #6c63ff;
}
.price__step_btn {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.price__step_btn .btn__blc {
  grid-column: 2/3;
}
.price__step_btn .btn__blc,
.price__step_btn .btn__wht {
  text-align: center;
}
.price__step_btn .btn__blc.dis,
.price__step_btn .btn__wht.dis {
  opacity: 0.5;
  pointer-events: none;
}
.price__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin: 20px 0 auto;
}
.price__wrapper .input__check {
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  padding: 15px;
  border-radius: 20px;
  z-index: 2;
  overflow: hidden;
}
.price__wrapper .input__check svg path {
  fill: #f1f1f1;
  transition: all 0.3s;
}
.price__wrapper .input__check p {
  margin: auto 0 0;
  font-size: 2.5em;
  transition: all 0.3s;
}
.price__wrapper .input__check .price__step_bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  border: 2px solid #f1f1f1;
  transition: all 0.3s;
  background: transparent;
  border-radius: 20px;
}
.price__wrapper .input__check input {
  width: 100%;
}
.price__wrapper .input__check input:checked + .price__step_bg {
  background: #EAE9FF;
  border: 2px solid #EAE9FF;
}
.price__wrapper .input__check input:checked + .price__step_bg + svg path {
  fill: #030303;
}
.price__wrapper .input__check input:checked + .price__step_bg + svg + p {
  color: #030303;
}
.price .price__wdt {
  grid-column: 1/3;
}
.price__final {
  display: flex;
  flex-direction: column;
}
.price__final .header__form_row {
  margin-bottom: auto;
}
.price__sum {
  font-size: 4.5em;
}
.price__descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #f1f1f1;
  margin: 20px 0 0;
}
.price__pag {
  position: absolute;
  bottom: 20px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.price__pag span {
  display: block;
  opacity: 0.5;
  background: #f1f1f1;
  transition: all 0.3s;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  transition: all 0.3s;
}
.price__pag span.active {
  opacity: 1;
}

.modal {
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s;
  overflow-x: auto;
  display: flex;
}
.modal.active {
  opacity: 1;
  pointer-events: all;
}
.modal__overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
  display: block;
  z-index: -1;
}
.modal .header__form {
  margin: auto;
  padding-top: 50px;
  position: relative;
  max-width: 400px;
}
.modal__close {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.modal__close::before, .modal__close::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #fff;
}
.modal__close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

@media (max-width: 1024px) {
  .header h1 {
    font-size: 6em;
  }
  .why__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .price__block {
    grid-template-columns: 1fr;
  }
  .faq__block {
    grid-template-columns: 1fr 1fr;
  }
  .faq__title {
    width: 100%;
  }
  .faq__wrapper {
    grid-column: auto;
  }
  .nav ul {
    gap: 15px;
  }
  .nav__logo {
    max-width: 160px;
  }
}
@media (max-width: 900px) {
  .nav__soc p {
    font-size: 2em;
  }
  .nav .container {
    flex-wrap: wrap;
  }
  .nav ul,
  .nav .btn__blc {
    display: none;
  }
  .nav__logo {
    max-width: 200px;
  }
  .nav__hum {
    display: block;
    width: 25px;
    height: 15px;
    margin: 0 0 0 auto;
    position: relative;
    cursor: pointer;
  }
  .nav__hum span, .nav__hum::before, .nav__hum::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: #000;
    position: absolute;
    top: 0;
    left: 0;
  }
  .nav__hum::before {
    top: 100%;
  }
  .nav__hum::after {
    top: 50%;
  }
  .nav.active {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    background: #f1f1f1;
    width: 100%;
    height: 100vh;
    overflow: auto;
    display: block;
  }
  .nav.active .container {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: auto 1fr auto auto;
  }
  .nav.active ul {
    margin: auto 0 auto;
    width: 100%;
    display: grid;
    justify-content: center;
    grid-column: 1/3;
  }
  .nav.active ul li {
    text-align: center;
  }
  .nav.active ul a {
    font-size: 20px;
  }
  .nav.active .btn__blc {
    display: block;
    margin: 20px 0 0;
    grid-column: 1/3;
  }
  .nav.active .nav__soc {
    display: block;
    width: 100%;
    margin: auto 0 0;
    grid-column: 1/3;
  }
  .nav.active .nav__soc p {
    text-align: center;
  }
  .nav.active .nav__soc div {
    justify-content: center;
  }
  .nav.active .nav__hum span {
    display: none;
  }
  .nav.active .nav__hum::before {
    top: 50%;
    left: 0%;
    transform: rotate(45deg);
  }
  .nav.active .nav__hum::after {
    top: 50%;
    left: 0%;
    transform: rotate(-45deg);
  }
}
@media (max-width: 768px) {
  body {
    font-size: 7px;
  }
  section {
    padding: 40px 0 0;
  }
  .faq__block {
    grid-template-columns: 1fr;
  }
  .faq__title .faq__title_text,
  .faq__title .btn__blc {
    display: none;
  }
  .faq__mob {
    display: block;
  }
  .faq .title {
    margin: 0;
  }
  .form .container {
    grid-template-columns: 1fr;
  }
  .header {
    padding: 40px 0;
  }
  .header__form {
    max-width: 100%;
  }
  .work {
    margin: 40px 0 0;
    padding: 40px 0;
  }
  .price {
    padding: 40px 0;
    margin: 40px 0 0;
  }
  .footer {
    padding: 40px 0 20px;
    margin: 40px 0 0;
  }
  .nav__soc p {
    font-size: 3em;
  }
  .footer__block {
    grid-template-columns: 1fr;
    align-items: start;
    gap: 20px;
  }
  .footer__logo {
    max-width: 140px;
  }
  .footer__list {
    margin: 0;
  }
  .footer__soc {
    flex-direction: row;
  }
  .footer__row {
    flex-direction: column;
    gap: 15px;
  }
}
@media (max-width: 600px) {
  .input__check p a {
    white-space: wrap;
  }
  .input__check p a::before, .input__check p a::after {
    content: none;
  }
  .title {
    margin: 0 0 20px;
    font-size: 4em;
  }
  .btn__blc, .btn__wht {
    width: 100%;
  }
  .header h1 {
    font-size: 5em;
  }
  .header__subtitle {
    font-size: 2.5em;
    margin: 20px 0 0;
  }
  .service__wrapper,
  .why__wrapper {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  .work__link svg,
  .work__link img {
    width: 24px;
    height: 24px;
  }
  .price__wrapper {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  .price .price__wdt {
    grid-column: auto;
  }
  .price__step_btn {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .price__step_btn .btn__blc {
    grid-column: auto;
    order: -1;
  }
  .form__soc a {
    width: 35px;
    height: 35px;
  }
  .form__soc a svg,
  .form__soc a img {
    width: 24px;
    height: 24px;
  }
}